*{
    color: #fffffe;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    scrollbar-color: #ff8906 #0f0e17;
    scrollbar-width: thin;
}
.hidden.hidden{
    display: none;
}
body{
    background-color: #0f0e17;
}
p{
    color: #a7a9be;
}
button{
    background-color: #ff8906;
    border: none;
    width: 200px;
    height: 60px;
    border-radius: 2px;
    align-self: center;
    margin-top: 25px;
    margin-bottom: 10px;
}
button:hover{
    background-color: #ffa033;
    cursor: pointer;
}
#main{
    display: flex;
    flex-direction: column;
    border: 2px dashed #fffffe;
}
form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
#form-caption{
    align-self: center;
}
#gen, #wrk, #edu{
    display: flex;
    align-items: center;
    flex-direction: column;
}
input{
    border: 2px solid white;
    background-color: transparent;
    height: 25px;
    width: 250px;
    padding-left: 5px;
}
label{
    margin: 10px 0px 10px 0px;
}
.cv{
    display: flex;
    flex-direction: column;
}
.general{
    margin-left: 100px;
    align-self: center;
    width: 400px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 23px;
    align-items: center;
}
.education{
    margin-left: 50px;
    width: 400px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 23px;
    align-items: center;
}
.work{
    align-self: flex-end;
    text-align: end;
    margin-right: 50px;
    width: 400px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 23px;
    align-items: center;
}
.middle{
    display: flex;
    justify-content: space-between;
}